import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs";
import { Appointment } from "./appointment.model";
import { HttpClient, HttpErrorResponse } from "@angular/common/http";
import { UnsubscribeOnDestroyAdapter } from "src/app/shared/UnsubscribeOnDestroyAdapter";
import { environment } from "src/environments/environment";
@Injectable()
export class AppointmentService extends UnsubscribeOnDestroyAdapter {

  isTblLoading = true;
  dataChange: BehaviorSubject<Appointment[]> = new BehaviorSubject<
    Appointment[]
  >([]);

  dialogData: any;
  constructor(private httpClient: HttpClient) {
    super();
  }
  get data(): Appointment[] {
    return this.dataChange.value;
  }
  getDialogData() {
    return this.dialogData;
  }

  /** CRUD METHODS */
  getAllAppointments(): void {
    this.subs.sink = this.httpClient.get<Appointment[]>(`${environment.apiUrl}/getAllAppointments`).subscribe(
      (data) => {
        this.isTblLoading = false;
        this.dataChange.next(data);
      },
      (error: HttpErrorResponse) => {
        this.isTblLoading = false;
        console.log(error.name + " " + error.message);
      }
    );
  }
  addAppointment(appointment: Appointment): void {
    this.dialogData = appointment;
     this.httpClient.post(`${environment.apiUrl}/addAppointment`, appointment).subscribe(data => {
      this.dialogData = appointment;
      },
      (err: HttpErrorResponse) => {
     // error code here
    });
  }
  updateAppointment(appointment: Appointment): void {
    this.dialogData = appointment;
    this.httpClient.put(`${environment.apiUrl}/updateAppointment` + appointment.id, appointment).subscribe(data => {
      this.dialogData = appointment;
    },
    (err: HttpErrorResponse) => {
    }
  );
  }
  deleteAppointment(id: number): void {
     this.httpClient.delete(`${environment.apiUrl}/deleteAppointment` + id).subscribe(data => {
      },
      (err: HttpErrorResponse) => {
      }
    );
  }
}
